___CSS_LOADER_EXPORT___.push([module.id, ".nav-bar[data-v-5d303b49]{position:relative}.dropdown-button[data-v-5d303b49]{background-color:gold;border-radius:8px;cursor:pointer;padding:10px;text-align:center}ul[data-v-5d303b49]{display:block;list-style-type:none;margin:0;padding:0}.nav-bar li[data-v-5d303b49]{background-color:gold;border-radius:8px;cursor:pointer;margin:10px;padding:10px;text-align:center}.nav-bar li.active[data-v-5d303b49],.nav-bar li[data-v-5d303b49]:hover{background-color:tomato}@media (max-width:1020px){.dropdown-button[data-v-5d303b49]{display:block}ul[data-v-5d303b49]{background-color:#fff;position:absolute;width:100%}ul[v-show=true][data-v-5d303b49]{display:block}}", "",{"version":3,"sources":["webpack://./resources/js/Components/Pages/Training/LiveStreamGuide/NavigationBar.vue"],"names":[],"mappings":"AA0FA,0BACE,iBACF,CAEA,kCAGE,qBAAyB,CACzB,iBAAkB,CAHlB,cAAe,CACf,YAAa,CAGb,iBACF,CAEA,oBAIE,aAAc,CAHd,oBAAqB,CAErB,QAAS,CADT,SAGF,CAEA,6BAEE,qBAAyB,CAGzB,iBAAkB,CAJlB,cAAe,CAEf,WAAY,CACZ,YAAa,CAEb,iBACF,CAEA,uEAEE,uBACF,CAEA,0BACE,kCACE,aACF,CAEA,oBAGE,qBAAuB,CAFvB,iBAAkB,CAClB,UAEF,CAEA,iCACE,aACF,CACF","sourcesContent":["<template>\n  <nav class=\"nav-bar text-black dark:text-gray-100 dark:bg-gray-800\">\n    <div @click=\"toggleDropdown\" class=\"fixed dropdown-button top-16 w-full\" v-if=\"appSettingStore.isSmallScreen\">\n      <font-awesome-icon icon=\"bars\" /> <!-- FontAwesome icon for menu -->\n      {{ currentSectionName }}\n    </div>\n    <ul v-show=\"dropdownOpen || !appSettingStore.isSmallScreen\">\n      <li v-for=\"(section, index) in sections\" :key=\"section.id\"\n          :class=\"{ 'active': section.id === activeSectionId }\"\n          @click=\"handleNavigation(section)\">\n        <font-awesome-icon :icon=\"`fa-${index + 1}`\" />\n        {{ section.name }}\n      </li>\n    </ul>\n  </nav>\n</template>\n<script setup>\nimport { ref, computed, watch, onMounted, onUnmounted } from 'vue'\nimport { useAppSettingStore } from \"@/Stores/AppSettingStore\"\nimport { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';\n\nconst appSettingStore = useAppSettingStore()\n\nconst props = defineProps({ sections: Array });\nconst emit = defineEmits(['navigate']);\n\nconst currentHash = ref(window.location.hash.replace('#', ''));\nconst dropdownOpen = ref(false);\n\n// appSettingStore.checkScreenSize()\n\n// Reactive window size check\nconst updateMobileState = () => {\n  // isMobile.value = window.innerWidth < 1020;\n  if (!appSettingStore.isSmallScreen) {\n    dropdownOpen.value = false; // Ensure dropdown is closed when not in mobile view\n  }\n};\n\n// window.addEventListener('resize', updateMobileState);\n\nconst activeSectionId = computed(() => currentHash.value || props.sections[0].id);\nconst currentSectionName = computed(() => {\n  const section = props.sections.find(s => s.id === activeSectionId.value);\n  return section ? section.name : 'Select a section';\n});\n\nfunction toggleDropdown() {\n  if (appSettingStore.isSmallScreen) { // Ensure toggle only works in mobile view\n    dropdownOpen.value = !dropdownOpen.value;\n  }\n}\n\nfunction handleNavigation(section) {\n  currentHash.value = section.id;\n  window.location.hash = section.id;\n  emit('navigate', section);\n  dropdownOpen.value = false; // Close dropdown after navigation\n}\n\n// Function to update the current hash based on window.location.hash\nfunction updateCurrentHash() {\n  currentHash.value = window.location.hash.replace('#', '');\n}\n\n\nwatch(() => window.location.hash, newHash => {\n  currentHash.value = newHash.replace('#', '');\n});\n\nonMounted(() => {\n  updateMobileState(); // Initial check on component mount\n  // Set up the hashchange event listener when the component mounts\n  window.addEventListener('hashchange', updateCurrentHash);\n  // if (!window.location.hash && props.sections.length > 0) {\n  //   window.location.hash = props.sections[0].id;\n  // }\n});\n\nonUnmounted(() => {\n  // Remove the hashchange event listener when the component unmounts\n  window.removeEventListener('hashchange', updateCurrentHash);\n});\n\n// Initial check in case the URL already has a hash when the component mounts\nupdateCurrentHash();\n</script>\n\n\n<style scoped>\n.nav-bar {\n  position: relative;\n}\n\n.dropdown-button {\n  cursor: pointer;\n  padding: 10px;\n  background-color: #FFD700;\n  border-radius: 8px;\n  text-align: center;\n}\n\nul {\n  list-style-type: none;\n  padding: 0;\n  margin: 0;\n  display: block; /* Always visible on larger screens */\n}\n\n.nav-bar li {\n  cursor: pointer;\n  background-color: #FFD700;\n  margin: 10px;\n  padding: 10px;\n  border-radius: 8px;\n  text-align: center;\n}\n\n.nav-bar li.active,\n.nav-bar li:hover {\n  background-color: #FF6347;\n}\n\n@media (max-width: 1020px) {\n  .dropdown-button {\n    display: block; /* Show on smaller screens */\n  }\n\n  ul {\n    position: absolute;\n    width: 100%;\n    background-color: white;\n  }\n\n  ul[v-show=\"true\"] {\n    display: block; /* Show when active */\n  }\n}\n</style>\n\n"],"sourceRoot":""}]);
